.button {
	width: 132px;
	height: 30px;
	border: none;
	border-radius: 3px;
	transition: width 0.4s;

	font-weight: 600;
	font-size: 14px;
	color: #fff;
}

.disabled {
	background: #004d99;
	opacity: 0.5;

	color: #002e5c;
}

.secondary {
	border: 1px solid #06c;
	width: 176px;
	color: #06c;
}

.default {
	background-color: #06c;
}

.red {
	background-color: #cc1439;
}

.default:hover {
	background-color: #0080ff;
	width: 167px;
}

.red:hover {
	background: #ff1a47;
	width: 167px;
}

.secondary:hover {
	background-color: #e5f0fa;
	width: 167px;
}

.default:active {
	background: #0059b2;
}

.secondary:active {
	background: #c9e5ff;
}

.red:active {
	background: #b21232;
}

.button:disabled {
}
