.input:focus {
	outline: none;
	border: 2px solid green;
}

.input {
	width: 372px;
	height: 36px;
	font-size: 20px;
	border: 1px solid #ccc;
	padding: 8px;
	box-sizing: border-box;
}

.inputWrapper {
	display: flex;
	flex-direction: column-reverse;
	gap: 5px;
}

.superInput {
	/* border: 1px solid green; */
}

.errorInput,
.errorInput:focus {
	border: 1px solid red;
}

.error {
	color: red;
}
